<template>
  <div class="text-xs-center">
    <v-pagination
      color="indigo"
      v-model="pageNum"
      :pageLength="pageLength"
      :length="this.pageLength"
      @input="changePageNum(pageNum)"
    ></v-pagination>
  </div>
</template>

<script>
export default {
  name: "Pagination",
  props: ["pageLength"],
  data() {
    return {
      pageNum: parseInt(this.$route.query.pageNum)
    };
  },
  methods: {
    changePageNum(pageNum) {
      this.pageNum = pageNum;
      this.$emit("change-page-num", this.pageNum);
    }
  },
  watch: {
    '$route.query.pageNum' : {
      handler(value) {
        this.pageNum = parseInt(value)
      }
    }
  }
};
</script>
