<template>
  <div id="navbar">
    <!-- <v-navigation-drawer app clipped light v-model="drawer">
      <v-divider></v-divider>
      <h3 id="filterTitle">Filters</h3>
      <v-divider></v-divider>
      <v-container>
      <api-types-filter
        @emitTest="apiFilter = $event"
        v-model="apiModelData"
        v-on:emitTest="higherEmit"
      />
      <developer-filter
        @devEmitTest="devApiFilter = $event"
        v-model="devApiFilter"
        v-on:emitTest="devHigherEmit"
      />
      </v-container>
    </v-navigation-drawer> -->

    <v-toolbar app color="#bfdff5" dark fixed clipped-left>
      <v-toolbar-title class="headline text-uppercase">
        <router-link to="/">
          <div id="logo">
            <span class="font-weight-bold black--text">AI</span>
            <span class="font-weight-thin black--text"> | Model Share</span>
          </div>
        </router-link>
<!--         <v-btn @click.stop="drawer =!drawer" outline color="indigo">Filters</v-btn>
        <router-link to="/search"><v-btn outline color="indigo">Search</v-btn></router-link> -->
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-text-field
        id="searchBar"
        flat
        solo-inverted
        hide-details
        prepend-inner-icon="search"
        placeholder="Search by API model, developer or type"
        v-model="searchString"
        @keyup.enter.native="onSubmit"
        v-if="showSearchBar"
      ></v-text-field>

      <v-btn class="black--text" icon @click="toggleSearchBar()">
        <i v-bind:class="getBtnClass()"></i>
      </v-btn>

      <a :href="uploadModelsUrl">
        <v-btn icon class="black--text">
          <i class="fas fa-upload"></i>
        </v-btn>
      </a>

      <router-link to="/login">
        <v-btn icon class="black--text">
          <i class="fas fa-user"></i>
        </v-btn>
      </router-link>
    </v-toolbar>
  </div>
</template>

<style scoped>
#filterTitle {
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 24px;
}
.headline a {
  text-decoration: none;
}
</style>

<script>
import apiTypesFilter from "../components/apiTypesFilter";
import developerFilter from "../components/developerFilter";
import { AmplifyEventBus } from "aws-amplify-vue";
import Constants from "../utils/constants.js";

export default {
  name: "Navigation",
  components: {
    apiTypesFilter,
    developerFilter
  },
  data() {
    return {
      apiFilter: "",
      apiModelData: "",
      devApiFilter: "",
      drawer: false,
      signedIn: false,
      showSearchBar: false,
      searchString: "",
      uploadModelsUrl: Constants.UPLOAD_MODELS_URL
    };
  },
  async beforeCreate() {
    try {
      await this.$Amplify.Auth.currentAuthenticatedUser();
      this.signedIn = true;
    } catch (err) {
      this.signedIn = false;
    }
    AmplifyEventBus.$on("authState", info => {      
      this.signedIn = info === "signedIn";
    });
  },
  methods: {
    higherEmit: function(data) {
      this.apiModelData = data;
    },
    devHigherEmit: function(data) {
      this.devApiFilter = data;
    },
    toggleSearchBar: function() {
      this.showSearchBar = !this.showSearchBar;
    },
    getBtnClass: function () {
        return {
          'fas fa-search': !this.showSearchBar,
          'fas fa-times': this.showSearchBar,
        }
    },
    onSubmit: function() {
      let finalSearchString = 'ALL';
      if (this.searchString.length > 0) {
        finalSearchString = this.searchString;
      }
      this.$router.push({name: 'Search', query: {search: finalSearchString, pageNum: "1"}});
    }
  }
};
</script>
