<template>
  <v-layout>
    <v-flex xs12>
      <v-card color="white" tile elevation-5>
        <v-card-text>
          <h2>Share, archive, and get credit for your machine learning models.</h2>
          <v-text-field
            label="Search by API Model, Developer, or Type"
            v-model="search"
            v-on:keyup.enter.prevent="submitSearch"
            hide-details
            prepend-icon="search"
            single-line
            clearable
            outline
          ></v-text-field>
          <v-btn
          color="indigo"
          class="white--text"
          @click.prevent="submitSearch">
          submit
          </v-btn>
        </v-card-text>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      search: ""
    };
  },
  methods: {
    submitSearch: function() {
      this.$emit("submitSearchTerm", this.search)
      this.search = ""
    },
  },
};
</script>

