<template>
  <div class="search">
    <v-container>
      <v-layout row wrap>
        <v-flex d-flex xs12 v-if="jsonData1 === 'no search results'">
        <v-card height="100%" color="white" id="apiFilter">
          <v-card-text>
            No Search Results currently available for those search terms, please use the filter drawer or search again.
          </v-card-text>
          </v-card>
        </v-flex>
        <v-flex d-flex xs12 v-for="item in jsonData1" v-if="item.modelname" id="apiFilter" class="mt-2">
          <v-card color="white" class="mx-auto d-block" max-width="600">
            <v-card-title primary-title class="mb-0">
              <div>
                <div class="headline">{{item.modelname}}</div>
                <div class="caption font-italic">{{item.apimodeltype}} model by {{item.apideveloper}}</div>
              </div>                  
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text class="pt-2">
              <p class="mb-1">{{item.apimodeldescription}}</p>
              <p>{{item.apimodelevaluation}}</p>
              <v-chip small dark v-for="tag in item.tags.split(',')" v-bind:key="tag">{{tag}}</v-chip>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn flat color="indigo" @click="goToDetail(item.modelname)">Details</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
      <Pagination
        v-on:change-page-num="changePageNum"
        :pageLength="pageLength"
      />
    </v-container>
  </div>
</template>

<script>
import Pagination from "../components/Pagination";
import Search from "../components/search";

export default {
  name: "Search",
  props: ["jsonData1", "pageLength"],
  data() {
    return {
      page: 1,
      searchTerm: "",
      searchTermData: ""
    };
  },
  components: {
    Pagination
  },
  beforeMount() {
  },
  methods: {
    submitSearchTerm(data) {
      this.searchTermData = data;
      this.createQueryString();
      this.searchTermData = "";
    },
    beforeRouteEnter(to, from, next) {
      this.createQueryString();
    },
    changePageNum: function(pageNum) {
      this.page = pageNum;
      this.createQueryString();
    },
    goToDetail(modName) {
      debugger
      this.$router.push({ name: "Detail", params: { modName: modName } });
    },
    getConsolidatedSearchParams: function() {
      let returnVal = {
        'search': this.searchTermData,
        'page': this.page
      }
      if (!this.searchTermData && this.$route.query.search) {
        returnVal.search = this.$route.query.search;
      }
      if (!this.page && this.$route.query.pageNum) {
        returnVal.page = this.$route.query.pageNum;
      }
      if (!returnVal.search) {
        returnVal.search = "ALL";
      }
      if (!returnVal.page) {
        returnVal.page = 1;
      }
      return returnVal;
    },
    createQueryString() {
      const route = {
        name: "Search"
      };
      let paramValues = this.getConsolidatedSearchParams();
      this.searchTermData = paramValues.search;
      this.page = paramValues.page;
      route.query = {
        search: this.searchTermData,
        pageNum: this.page
      }
      this.$router.push(route);
    },
    beforeRouteLeave: function () {
      this.searchTermData = null;
      this.page = null;
    },
    watch: {
      "$route.query.pageNum": {
        immediate: true,
        handler(value) {
          this.page = value;
        },
      },
      "$route.query.search": {
        immediate: true,
        handler(value) {
          this.searchTermData = value;
        }
      }
    }
  }
};
</script>

