<template>
  <v-app>
    <Navigation
      @emitTest2="modFilter = $event"
      v-on:emitTest2="filterPage"
      @devEmitTest2="devFilter = $event"
      v-on:devEmitTest2="devFilterPage"
    />

    <v-content>
      <router-view
        :jsonData1="jsonData"
        :appPage="page"
        :modelFilter="apimod"
        :devFilter="devFilter"
        :pageLength="pageLength"
      ></router-view>
    </v-content>
  </v-app>
</template>

<script>
import axios from "axios";
import Navigation from "./components/Navigation";

export default {
  name: 'App',
  components: {
    Navigation
  },
  data () {
    return {
      jsonData: "",
      apidev: "ALL",
      apimod: "ALL",
      searchItem: "ALL",
      pageLength: 0,
      dataLength: 0,
      modFilter: "",
      devFilter: "",
      apiModelFilter: [],
      tag: "ALL",
      pageSize: 10,
      page: 1
    };
  },
  methods: {
    getPage: function() {
      axios({
        method: "GET",
        url:
          "https://uql72jx73a.execute-api.us-east-1.amazonaws.com/dev/todos?id=ALL&apideveloper=" +
          this.apidev +
          "&apimodeltype=" +
          this.apimod +
          "&searchitems=" +
          this.searchItem +
          "&tags=" +
          this.tag +
          "&pagesize=" +
          this.pageSize +
          "&pagenumber=" +
          this.page
      }).then(
        result => {
          this.modelTypes = [];
          this.singlemodelTypes = [];
          this.jsonData = result.data;
          console.log("results: " + result.data[0])
          for (let i = 0; i < result.data.length; i++) {
            if (result.data[i].apimodeltype !== undefined) {
              this.modelTypes.push(result.data[i].apimodeltype.toLowerCase());
            }
          }
          this.pageLength = Math.ceil(result.data[0] / 10);
          this.singlemodelTypes = new Set(this.modelTypes);
          this.apiModelFilter = [...this.singlemodelTypes];
        },
        error => {
          this.jsonData = "no search results";
          console.log(error);
        }
      );
    },
    filterPage(data) {
      this.apimod = data;
      this.getPage();
    },
    devFilterPage(data) {
      this.apidev = data;
      this.getPage();
    }
  },
  watch: {
    "$route.query.search": {
      immediate: true,
      handler(value) {
        var that = this;
        setTimeout(function() {
          if (value) {
            that.searchItem = value;
          } else {
            that.searchItem = "ALL";
          }
          that.getPage();
        }, 1000);
      }
    },
    '$route.query.pageNum': {
      immediate: true,
      handler(value) {
        if (value) {
          this.page = value;
        } else {
          this.page = 1;
        }
        this.getPage();
      }
    },
  }
}
</script>
