<template>
  <div>
    <v-select
      :items="apiDeveloperFilter"
      label="Api Developers"
      outline
      browser-autocomplete
      background-color="indigo"
      color="indigo"
      item-value="text"
      v-model="apiDevData"
      v-on:input="devFilter"
    >{{ apiDeveloperFilter }}</v-select>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "developerFilter",
  data() {
    return {
      jsonData: "",
      apiDeveloperFilter: [],
      apiDevData: "",
      all: "ALL",
    };
  },
  methods: {
     devFilter: function(event) {
      console.log(event);
      this.apiDevData = event;
      this.$emit("emitTest", this.apiDevData);
      
    },
    getPage: function() {
      axios({
      method: "GET",
      url:
          "https://uql72jx73a.execute-api.us-east-1.amazonaws.com/dev/todos?id=ALL&apideveloper=ALL&apimodeltype=ALL&searchitems=ALL&tags=ALL&pagesize=1000&pagenumber=1"
    }).then(
      result => {
        this.devTypes = [];
        this.singleDevTypes = [];
        this.jsonData = result.data;
        for (let i = 0; i < result.data.length; i++) {
            if (result.data[i].apideveloper !== undefined){
          this.devTypes.push(result.data[i].apideveloper);
            }
        }

        // console.log(this.modelTypes);
        this.singleDevTypes = new Set(this.devTypes);
        // console.log(this.singlemodelTypes)
        this.apiDeveloperFilter = [...this.singleDevTypes];
        this.apiDeveloperFilter.push("ALL");
      },
      error => {
        console.log(error);
      }
    );
    }
  },
  mounted() {
    this.getPage()
  }
};
</script>


