<template>
  <div>

    <v-select
      :items="apiModelFilter"
      label="Api Types"
      outline
      browser-autocomplete
      background-color="indigo"
      color="indigo"
      item-value="text"
      v-model="apiModelData"
      v-on:input="logModel">
    {{ apiModelFilter }}
    
    </v-select>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "apiTypesFilter",
  data() {
    return {
      jsonData: "",
      apiModelFilter: [],
      apiModelData: "",
    };
  },
  methods: {
    logModel: function(event) {
      console.log(event);
      this.apiModelData = event;
      this.$emit("emitTest", this.apiModelData);
      
    },
    getPage: function() {
      axios({
        method: "GET",
        url:
          "https://uql72jx73a.execute-api.us-east-1.amazonaws.com/dev/todos?id=ALL&apideveloper=ALL&apimodeltype=ALL&searchitems=ALL&tags=ALL&pagesize=1000&pagenumber=1"
      }).then(
        result => {
          this.modelTypes = [];
          this.singlemodelTypes = [];
          this.jsonData = result.data;
          for (let i = 0; i < result.data.length; i++) {
            if (result.data[i].apimodeltype !== undefined){
              this.modelTypes.push(result.data[i].apimodeltype);
            }
            
          }

          this.singlemodelTypes = new Set(this.modelTypes);
          this.apiModelFilter = [...this.singlemodelTypes];
          this.apiModelFilter.push("ALL");
        },
        error => {
          console.log(error);
        }
      );
    }
  },
  mounted() {
      this.getPage();
  }
};
</script>
  

